<template>
  <div>
    <div class="login">
      <v-form v-model="valid">
        <v-text-field variant="outlined" label="아이디" required></v-text-field>
        <v-text-field
          variant="outlined"
          hide-details="false"
          :append-inner-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show1 ? 'text' : 'password'"
          label="비밀번호"
          @click:appendInner="show1 = !show1"
          required
        ></v-text-field>
        <v-checkbox label="로그인 상태 유지"></v-checkbox>
        <v-btn style="width: 100%; background-color: slateblue; color: white" class="mr-4" @click="validate">
          로그인
        </v-btn>
      </v-form>
    </div>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      valid: true,
      show1: false
    }
  },
  setup() {},
  created() {},
  mounted() {},
  unmounted() {},
  methods: {
    validate() {
      this.$router.push('/main') // 가장 기본적인 방법
    }
  }
}
</script>
<style scoped>
.login {
  max-width: 400px;
  min-width: 200px;
  width: 40%;
  margin: auto;
  margin-top: 10%;
}
</style>
