import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import { loadFonts } from './plugins/webfontloader'
import tuicss from 'tui-date-picker/dist/tui-date-picker.css'
import tuigrid from 'tui-grid/dist/tui-grid.css'
import tuipage from 'tui-pagination/dist/tui-pagination.css'
import mystyle from '@/style/main.css'

loadFonts()

createApp(App)
  .use(router)
  .use(store)
  .use(vuetify)
  .use(tuicss)
  .use(tuigrid)
  .use(tuipage)
  .use(mystyle)
  .mount('#app')
