import { createRouter, createWebHistory } from 'vue-router'
import loginView from '../views/LoginView.vue'

const routes = [
  {
    path: '/',
    name: 'login',
    component: loginView
  },
  {
    path: '/main',
    name: 'main',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "main" */ '../views/MainView.vue'),
    children: [
      {
        path: 'createDataChangeReq',
        name: 'main.createDataChangeReq',
        component: () => import('@/components/sub/CreateDataChangeReq.vue')
      },
      {
        path: 'createDataChangeQuery',
        name: 'main.createDataChangeQuery',
        component: () => import('@/components/sub/CreateDataChangeQuery.vue')
      },
      {
        path: 'waitApprovalReq',
        name: 'main.waitApprovalReq',
        component: () => import('@/components/sub/WaitApprovalReq.vue')
      },
      {
        path: 'statusDataChangeReq',
        name: 'main.statusDataChangeReq',
        component: () => import('@/components/sub/StatusDataChangeReq.vue')
      },
      {
        path: 'manageDBInformation',
        name: 'main.manageDBInformation',
        component: () => import('@/components/sub/ManageDBInformation.vue')
      }
    ]
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
